.reveal-text2::after {
	content: "";
	position: absolute;
	z-index: 999;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-color: #fff;
	-webkit-transform: scaleX(0);
	-ms-transform: scaleX(0);
	    transform: scaleX(0);
	-webkit-transform-origin: 0 50%;
	-ms-transform-origin: 0 50%;
	    transform-origin: 0 50%;
	pointer-events: none;
	-webkit-animation-name: revealer-text2;
	animation-name: revealer-text2;
}




